<template>
  <div class="recommend">
    <div class="title">
      <span class="text">推荐歌单</span>
      <van-button @click="toMore" round type="default">更多 ></van-button>
    </div>
    <div class="content">
      <div  @click="toPlayListDetail(item.id)" class="item" v-for="item in recommendList" :key="item.id">
        <img :src="item.picUrl+'?param=150y150'" /><br />
        <span class="text">{{ item.name1 }}</span
        ><br />
        <span class="text">{{ item.name2 }}</span>
        <span class="iconfont icon-caret-right">{{ item.playCount }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getRecommend } from "../../../NetRequest/discover";
import { toStringNum } from "../../../common/common.js";
export default {
  data() {
    return {
      recommendList: [],
    };
  },
  async mounted() {
    let localData = JSON.parse(localStorage.getItem("recommendList"));
    if (localData) {
      // console.log("使用缓存数据");
      this.recommendList = localData;
    } else {
      const res = await getRecommend(10);
      // console.log(res);
      // this.recommendList = res.data.result;
      for (const item of res.data.result) {
        this.recommendList.push({
          id: item.id,
          name1: item.name.substr(0, 8),
          name2: item.name.substr(9, item.name.length),
          picUrl: item.picUrl,
          playCount: toStringNum(item.playCount),
        });
      }
      // console.log(this);
      localStorage.setItem("recommendList", JSON.stringify(this.recommendList));
    }
  },
  methods: {
    /* 更多 页面 */
    toMore(){
      this.$router.push('/discover/recommendMore')
    },
    /* 歌单详情 */
    toPlayListDetail(id) {
      // console.log(id);
      this.$router.push({
        path: "/recommendMore/playListDetail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style scoped>
.content {
  white-space: nowrap;
  overflow: scroll;
  scrollbar-width: none;
}
.content::-webkit-scrollbar {
  display: none;
}
.recommend .title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.title .text {
  font-weight: 800;
}
.title .van-button {
  height: 30px;
}
.content .item {
  position: relative;
  width: 100px;
  height: 140px;
  margin-right: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}
.item img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
}
.item span {
  font-size: 10px;
}
.item .iconfont {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 50px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  font-size: 10px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
</style>