<template>
  <div class="newSongs">
    <div class="title">
      <span class="text">新歌速递</span>
      <van-button @click="toMore" round type="default">更多 ></van-button>
    </div>
    <van-swipe :loop="false" :show-indicators="false" :width="300">
      <van-swipe-item v-for="(item1, i) in 3" :key="item1">
        <el-card class="box-card">
          <div class="items" v-for="(item2,index) in newSongs[i]" :key="item2.id">
            <div class="left" @click="playThis(item2.id,i,index)">
              <img :src="item2.picUrl+'?param=100y100'" />
              <div class="name">
                <p>{{ item2.name }}</p>
                <p>{{ item2.artists }}</p>
              </div>
            </div>
            <!-- <div class="playBtn">
              <span class="iconfont icon-bofang"></span>
            </div> -->
          </div>
        </el-card>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { getNewSongs } from "../../../NetRequest/discover";
import { findItem } from "../../../common/common"
export default {
  data() {
    return {
      newSongsList: [],
      hasClcikMusicId:''
    };
  },
  async created() {
    let localData = JSON.parse(localStorage.getItem("newSongsList"));
    if (localData) {
      // console.log("使用缓存数据");
      this.newSongsList = localData;
    } else {
      const res = await getNewSongs(0);
      // console.log(res);
      for (const item of res.data.data) {
        this.newSongsList.push({
          id: item.id,
          name: item.name,
          artists: item.artists[0].name,
          picUrl: item.album.picUrl,
        });
      }
      localStorage.setItem("newSongsList", JSON.stringify(this.newSongsList.slice(0,9)));
    }
  },
  methods: {
    group(array, subGroupLength) {
      let index = 0;
      let newArray = [];
      while (index < array.length) {
        newArray.push(array.slice(index, (index += subGroupLength)));
      }
      return newArray;
    },
    
    /* 播放当前点击歌曲 */
    playThis(musicId,i,idx){
      // console.log(musicId);
      const index = findItem(this.$store.state.playList,'id',musicId)
      if(index != -1){//如果播放列表有这首歌
        if(this.hasClcikMusicId == musicId){
          //如果被点击这首歌已经被点击过且正在播放
          this.$store.state.isPlaying = true
          this.$bus.$emit('handlePlay')
          this.hasClcikMusicId = ''
          return
        }
        this.$bus.$emit('receiveMusicId',musicId,index)
      } else{
        //播放列表没有这首歌
        const newIndex = this.$store.state.playList.length
        this.$store.state.playList.push(this.newSongs[i][idx])
        // console.log(this.$store.state.playList);
        this.$bus.$emit('receiveMusicId',musicId,newIndex)
      }
      this.hasClcikMusicId = musicId
    },
    /* 前往更多页面 */
    toMore(){
      this.$router.push('/discover/newSongsMore')
    }
  },
  computed: {
    newSongs() {
      return this.group(this.newSongsList, 3);
    },
  },
};
</script>

<style scoped>
.newSongs .title {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}
.title .text {
  font-weight: 800;
}
.title .van-button {
  height: 30px;
}
.box-card {
  margin-top: 10px;
  margin-right: 10px;
  border-radius: 10px;
}
.box-card .items {
  display: flex;
  justify-content: space-between;
}
.items .left {
  display: flex;
  justify-content: space-between;
}
.left .name {
  margin-left: 10px;
  width: 170px;
}
.left .name p {
  margin: 0;
  line-height: 25px;
  text-align: left;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.left .name p:last-child {
  font-size: 10px;
  color: #999;
}
.items .iconfont {
  line-height: 50px;
  text-align: center;
  font-size: 25px;
}
.items img {
  width: 50px;
  height: 50px;
  border-radius: 5px;
}
.items:nth-child(2) {
  margin: 10px 0;
}
</style>