<template>
  <div class="banner">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item, index) in imgList" :key="index">
        <van-image
          :src="item.pic+'?param=500y500'"
        />
        <div class="typeTitle" :style="{
                background:
                  item.titleColor === 'blue' ? '#326ef8' : item.titleColor,
              }">
            <span>{{item.typeTitle}}</span>
        </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { getBanner } from "../../../NetRequest/discover";
export default {
  data() {
    return {
      imgList: [],
    };
  },
  async created() {
    let localData = JSON.parse(localStorage.getItem("imgList"))
    if (localData) {
      // console.log("使用缓存数据");
      this.imgList = localData;
    } else {
    const res = await getBanner(1)
      // console.log(res);
    this.imgList = res.data.banners;
    localStorage.setItem("imgList", JSON.stringify(this.imgList));
    }
  },
};
</script>
<style>
.my-swipe{
    border-radius: 15px;
}
.van-swipe-item{
    width: 100%;
    height: 100%;
}
.van-image{
    width: 100%;
    height: 100%;
    position: relative;
}
.typeTitle{
    position: absolute;
    right: 0;
    bottom: 3px;
    padding: 5px;
    border-top-left-radius: 10px;
    background-color: #fff;
}
</style>