<template>
  <div class="MV">
    <div class="title">
      <span class="text">最新MV</span>
      <van-button @click="toMore" round type="default">更多 ></van-button>
    </div>
    <div class="content">
      <el-card v-for="(item,index) in mvList" :key="item.id" :body-style="{ padding: '0px' }">
        <img
          :src="item.cover+'?param=200y200'"
          class="image"
        />
        <span class="iconfont icon-caret-right">{{item.playCount}}</span>
        <span  @click="playMv(index)" class="iconfont icon-bofang"></span>
        <div class="name">
          <span>{{item.name}}</span><br>
          <span class="auther">{{item.artistName}}</span>
        </div>
      </el-card>
    </div>
    <van-popup
      :style="{ height: '100%', width: '100%' }"
      v-model="$store.state.showPlayMv"
    >
      <playMv v-if="$store.state.showPlayMv" ref="getData"/>
    </van-popup>
  </div>
</template>

<script>
import { getMV } from "../../../NetRequest/discover";
import { toStringNum } from "../../../common/common.js"
import playMv from "../../../components/MvTopMore/playMv";
export default {
  components: { playMv },
  data() {
    return {
      mvList: [],
      topOffset:''
    };
  },
  async mounted() {
    this.$bus.$on("closeModle", this.closeModle);
    let localData = JSON.parse(localStorage.getItem("mvList"))
    if (localData) {
      // console.log("使用缓存数据");
      this.mvList = localData;
    } else {
    const res = await getMV("", 10)
      // console.log(res);
      for (const item of res.data.data) {
        this.mvList.push({
          id: item.id,
          name: item.name, // mv名称
          cover: item.cover, // mv封面
          artistName: item.artistName, // 创作者
          playCount: toStringNum(item.playCount), // 播放量
        });
      }
      localStorage.setItem("mvList", JSON.stringify(this.mvList));
    }
  },
  methods: {
    /* 前往更多页面 */
    toMore(){
      this.$router.push('/discover/MvTopMore')
    },
    playMv(index) {
      // console.log(index);
      this.$store.state.isPlaying = true
      this.$bus.$emit('handlePlay')
      this.$store.state.palyMvInfo = this.mvList[index];
      this.$store.state.showPlayMv = true;
      this.stopBodyScroll(true);
      setTimeout(()=>{
        // console.log('setTimeout')
        this.$refs.getData.getData()
      },1500)
      document.body.addEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "fixed";
      document.querySelector("body").style.width = "100%";
      this.$store.state.showControlBar = false
    },
    bodyScroll(event) {
      event.preventDefault();
    },
    stopBodyScroll(isFixed) {
      let body_scroll = document.querySelector("body");
      if (isFixed) {
        this.topOffset = window.scrollY;
        body_scroll.style.position = "fixed";
        body_scroll.style.top = -this.topOffset + "px";
      } else {
        body_scroll.style.position = "";
        window.scrollTo(0, this.topOffset);
      }
    },
    closeModle() {
      // console.log('closeModle');
      this.stopBodyScroll(false);
      document.body.removeEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "initial";
      document.querySelector("body").style.height = "auto";
    },
  },
};
</script>

<style scoped>
.MV {
  margin-top: 15px;
}
.MV .title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.title .text {
  font-weight: 800;
}
.title .van-button {
  height: 30px;
}
.content {
  white-space: nowrap;
  overflow: scroll;
  scrollbar-width: none;
}
.content::-webkit-scrollbar {
  display: none;
}
.el-card {
  position: relative;
  width: 150px;
  display: inline-block;
  margin-right: 10px;
}
.auther {
  font-size: 13px;
  color: #999;
}

.image {
  width: 150px;
  height: 100px;
  display: block;
}

.icon-caret-right {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #eee;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 3px;
  border-radius: 5px;
}
.icon-bofang {
  position: absolute;
  top: 35px;
  left: 60px;
  font-size: 30px;
  color: #eee;
}
.name{
    padding: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
</style>