<template>
  <div class="hotSinger">
    <div class="title">
      <span class="text">热门歌手</span>
      <van-button @click="toMore" round type="default">更多 ></van-button>
    </div>
    <div class="content">
      <el-card v-for="item in hotSingerList" :key="item.id" class="box-card">
        <img @click="toDetail(item.id)" :src="item.picUrl+'?param=200y200'" />
        <br />
        <span class="name">{{item.name}}</span>
      </el-card>
      <div class="footer">
        <span>~ ~ 已经到底啦</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getHotSinger } from "../../../NetRequest/discover";
export default {
  data() {
    return {
      hotSingerList:[]
    }
  },
  async created() {
    let localData = JSON.parse(localStorage.getItem("hotSingerList"))
    if (localData) {
      // console.log("使用缓存数据");
      this.hotSingerList = localData;
    } else {
      const res = await getHotSinger(9)
      // console.log(res)
      for (const item of res.data.artists) {
        this.hotSingerList.push({
          id: item.id,
          name: item.name,
          picUrl: item.img1v1Url
        });
      }
      localStorage.setItem("hotSingerList", JSON.stringify(this.hotSingerList));
    }
  },
  methods: {
    toMore(){
      this.$router.push('/discover/singerTopMore')
    },
    toDetail(singerId){
      this.$store.state.singerId = singerId
      this.$router.push('/discover/singerTopMore/singerDetailMore')
    }
  },
};
</script>

<style scoped>
.hotSinger .title {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}
.title .text {
  font-weight: 800;
}
.title .van-button {
  height: 30px;
}
.content{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 120px;
}
.box-card {
  position: relative;
  width: 110px;
  height: 130px;
  margin-bottom: 10px;
}
img {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.name {
  position: relative;
  bottom: -70px;
  font-size: 13px;
  width: 100%;
  padding: auto;
  text-align: center;
  display: block;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.footer{
  width: 100%;
  height: 20px;
  font-size: 15px;
  color: #999;
  text-align: center;
}
</style>